<template>
	<header :class="`flex-xb ${navShowFlag ? 'open' : ''}`">
		<div class="flex-yc">
			<router-link
				to="/"
				class="logo pic"
			>
				<img
					:src="getLogo"
					alt="logo"
				/>
			</router-link>

			<div class="center">
				<slot name="title"></slot>
			</div>
		</div>
		<div
			class="navbar-toggle"
			title="导航：点击打开"
			@click="switchNav"
		>
			<span class="line-top"></span>
			<span class="line-middle"></span>
			<span class="line-bottom"></span>
		</div>
		<div class="navGroup flex-yc">
			<router-link
				v-for="(item, index) in List"
				:key="index"
				@click="itemTo(item)"
				:to="item.path"
			>
				<div class="icon">
					<img
						:src="item.meta.icon"
						alt=""
					/>
				</div>
				<span>{{ item.meta.title }}</span>
			</router-link>
		</div>
	</header>
</template>

<script>
import { routes } from '@/router/routes';
export default {
	props: {
		logoType: {
			default: 'tool',
			type: String,
		},
	},
	computed: {
		getLogo() {
			let logo = '';
			switch (this.logoType) {
				case 'blog':
					logo = require('@/assets/img/logo.svg');
					break;
				default:
					logo = require('@/assets/img/logo-tool.svg');
					break;
			}
			return logo;
		},
	},
	data() {
		return {
			navShowFlag: true,
			List: [],
		};
	},
	created() {
		this.List = routes.filter((x) => x.meta.homeShow !== false);

		setTimeout(() => {
			this.switchNav();
		}, 1000 * 10);
	},
	methods: {
		switchNav() {
			this.navShowFlag = !this.navShowFlag;
		},
	},
};
</script>
<style>
header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 10px 10px;
	background: #fff;
	z-index: 50;
	box-shadow: 0 0 5px #999;
}

header .center {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	margin-left: 0;
	transition: all ease 0.5s;
}

header.open .center {
	left: 25%;
}

header .center h1 {
	font-size: 18px;
}

.logo {
	height: 100%;
	font-size: 18px;
	display: block;
}

.logo img {
	width: auto;
	height: 28px;
}

.navGroup {
	position: absolute;
	right: 50px;
	top: 0%;
	height: 48px;
	opacity: 0;
	pointer-events: none;
	transition: all ease 0.3s;
	z-index: 50;
	background: #fff;
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
}

.open .navGroup {
	opacity: 1;
	pointer-events: visible;
}

.navGroup a {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding: 5px 10px;
	border-radius: 3px;
	font-size: 14px;
	line-height: 1;
	background: #fff;
	color: #000;
	border: 1px solid #000;
	transition: all ease 0.2s;
}

.navGroup a:hover,
.navGroup a.nuxt-link-exact-active {
	opacity: 0.7;
}
.navGroup .icon {
	width: 25px;
	margin-right: 10px;
}
.navbar-toggle {
	margin-left: 20px;
}

/* 
*======================================================media===============================================================
*/
@media screen and (max-width: 991px) {
	.navGroup {
		position: fixed;
		width: 100%;
		height: 100%;
		padding: 10vh 5%;
		flex-direction: column;
		align-items: flex-start;
		left: 0;
		bottom: 0;
		z-index: 44;
	}

	.navGroup a {
		width: 100%;
	}
}
</style>
