<template>
  <div
    class="mediaTool _page"
    :theme="form.theme"
  >
    <Header>
      <div
        slot="title"
        class="flex-yc"
      >
        <h1>自适应计算器</h1>
        <div
          class="openSetting el-icon-setting"
          @click="dialogVisible = true"
        ></div>
      </div>
    </Header>
    <div class="configUrl flex-xb">
      <el-dialog
        title="设置"
        :visible.sync="dialogVisible"
        width="50%"
        :before-close="handleClose"
      >
        <el-form
          ref="form"
          :model="form"
          label-width="4em"
        >
          <el-form-item label="网址">
            <el-input v-model="form.url"> </el-input>
          </el-form-item>
          <el-form-item label="主题">
            <el-switch v-model="form.theme"></el-switch>

            <span style="margin-left: 20px">
              <el-tag
                v-if="form.theme"
                type="info"
                >黑夜</el-tag
              >
              <el-tag v-else>白天</el-tag>
            </span>
          </el-form-item>
          <el-form-item label="高度">
            <el-switch v-model="form.allHeight"></el-switch>
            <span style="margin-left: 20px">
              <el-tag
                v-if="form.allHeight"
                type="info"
                >自由</el-tag
              >
              <el-tag v-else>限制</el-tag>
            </span>
          </el-form-item>
        </el-form>

        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="SaveConfig()">已自动保存点击关闭</el-button>
          <el-button
            type="primary"
            @click="SaveConfig(true)"
            >记住配置
          </el-button>
        </span>
      </el-dialog>
    </div>
    <div class="view-box">
      <ul class="flex">
        <li
          v-for="(item, index) in userOption"
          :key="index"
          :style="{
            width: item.size[0] + 'px',
            height: form.allHeight ? item.size[1] + 'px' : '667px',
          }"
        >
          <div class="info">
            <h2>{{ item.name }}</h2>
            <p>
              {{ item.size[0] }}
              *
              {{ form.allHeight ? item.size[1] : 667 }}
            </p>
          </div>
          <div class="iframe-box">
            <iframe
              v-if="form.url"
              :src="form.url"
              frameborder="0"
            ></iframe>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import 'Css/mediaTest.css';
import Header from '@/components/Header.vue';
export default {
  name: 'mediaTest',
  components: { Header },
  data() {
    return {
      dialogVisible: false,
      form: {
        theme: false,
        allHeight: true,
      },
      defaultOption: {
        phone: [
          {
            name: 'iphone SE',
            size: [375, 667],
          },
          {
            name: 'XiaoMi 11',
            size: [360, 800],
          },
          {
            name: 'iphone 12Pro',
            size: [390, 844],
          },
          {
            name: 'iphone XR',
            size: [414, 896],
          },
        ],
        pad: [
          {
            name: 'iPad Air',
            size: [820, 1180],
          },
          {
            name: 'iPad Mini',
            size: [768, 1024],
          },
        ],
      },
      userOption: [
        {
          name: 'iphone SE',
          size: [375, 667],
        },
        {
          name: 'XiaoMi 11',
          size: [360, 800],
        },
        {
          name: 'iphone 12Pro',
          size: [390, 844],
        },
        {
          name: 'iphone XR',
          size: [414, 896],
        },
      ],
    };
  },
  created() {
    let OldUserConfig = localStorage.getItem('userConfig');
    if (OldUserConfig) {
      this.$message('已从缓存中恢复配置');
      console.log(JSON.parse(OldUserConfig));
      this.form = JSON.parse(OldUserConfig);
    }
  },
  methods: {
    SaveConfig(remember) {
      this.dialogVisible = false;
      this.$forceUpdate();
      if (remember) {
        localStorage.setItem('userConfig', JSON.stringify(this.form));
      }
    },
    handleClose(done) {
      this.dialogVisible = false;
    },
  },
};
</script>

<style></style>
